body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: lighter;
}


body {
    box-sizing: border-box;
    padding: 0;
    margin: 0;

    /* background: url(../assets/cycling-pexels-nubia-navarro-\(nubikini\)-386024.jpg); */
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
}

.login-backdrop {
    width: 100%;
    height: 100%;
    background-color: #131D28;
    opacity: 0.6;
    position: absolute;
    /* z-index: 1; */
}

.container-props {
    position: fixed;
    /* z-index: 5; */
    top:40%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.container-props .brand-icon {
    font-size: 3rem;
    font-family: 'Lobster';
    color: orange;
}

.form-col-props {
    height: 550px;
    /* border: 1px solid rgba(255, 255, 255, 0.5); */
    background-color: rgba(11, 18, 24, 0.7);
    padding: 5rem 2rem 3rem 2rem !important;
}

.form-col-props-signup {
    height: 600px;
    /* border: 1px solid rgba(255, 255, 255, 0.5); */
    padding: 3rem 2rem 2.5rem 2rem !important;
    background-color: rgba(11, 18, 24, 0.7);
}

.login-btn-props {
    width: 100%;
    text-transform: uppercase;
    letter-spacing:2px;
    font-size: 14px;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    padding: 10px;
    background-color: rgba(255,165,0, 1);
    color: white;
    border: 1px solid rgba(255,165,0, 1);
}

.login-input-props {
    border:none;
    border-radius: 25px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    padding: 10px 15px;
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
    font-size: 18px;
}

.links {
    padding: 30px 0 15px 15px;
}

.links .signup-link {
    text-transform: capitalize;
    font-size: 15px;
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;
    font-size: 20px;
}






/* PSEUDO CLASSES */

.login-btn-props:hover {
    background-color: rgba(255, 255, 255, 0.7);
    border-color: rgba(255, 255, 255, 0.7);
    color: #222;
}

.login-input-props::-webkit-input-placeholder {
    color: #ccc;
    font-size: 18px;
    padding: 10px;
}

.login-input-props:-ms-input-placeholder {
    color: #ccc;
    font-size: 18px;
    padding: 10px;
}

.login-input-props::placeholder {
    color: #ccc;
    font-size: 18px;
    padding: 10px;
}

.btn:focus, 
.login-btn-props:focus {
    box-shadow: none;
}

.login-input-props:focus {
    color: white;
    background-color: transparent;
    border: 1px solid orange;
    outline: 0px;
    box-shadow: none;
    font-size: 18px;
}
:root {
    --main: orange;
    --bg: #0B1218;
    --bg-body: #131D28;
}

* {
    font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    box-sizing: border-box;
    text-decoration: none;
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-weight: lighter;
}

body {
    background: #131D28;
    background: var(--bg-body);
}

.add-task-container .card {
    background: #0B1218;
    background: var(--bg);
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
}

.add-task-container form {
    color: white;
}

.add-task-container form label {
    font-weight: normal;
}

.task-heading {
    background: #0B1218;
    background: var(--bg);
    padding: 20px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
}

.task-heading h1 {
    font-weight: lighter;
}

form h3 {
    font-weight: lighter;
}

#userTasks {
    height: 40px;
    border-radius: 5px;
    background-color: white;
    padding: .375rem .75rem;
}

form .btn {
    background: orange;
    background: var(--main);
    color: white;
    width: 200px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    font-size: 18px;
}



/* PSEUDOS */


.btn.focus, .btn:focus {
    outline: 0;
    box-shadow: 0 0 0 .2rem rgba(255,165,0,.25);
}

.form-control:focus {
    color: rgb(73, 80, 87);
    background-color: rgb(255, 255, 255);
    border-color: #ced4da;
    outline: 0px;
    box-shadow: none;
}
